




import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class HelloWorld extends Vue {
  @Prop() v: number;
  @Prop() max: number;
  @Prop() min: number;

  get color() {
    return (
      'r' +
      Math.min(
        3,
        Math.max(
          -3,
          Math.round(
            (this.v - this.min - (this.max - this.min) / 2) /
              (this.max - this.min) *
              7
          )
        )
      )
    );
  }
}
